import React, { useEffect, useState } from 'react'
import './index.css'
import axios from 'axios'
import FA from 'react-fontawesome';
// import { useNavigate } from 'react-router-dom'
import { saveAs } from 'file-saver'

const App = () =>{

  const base_url = '';

  const [photo, setPhoto] = useState([])
  const [load, setLoad] = useState(false)
  const [file, setFile] = useState()
  const [name, setName] = useState('')

  const handleUpld = async (e) =>{
    e.preventDefault();
    setLoad(true)
    setName(file.name);

    let formData = new FormData();
    formData.append('image', file); 
    formData.append('name', name);

    // console.log(formData)
    
    
    const fetchData = async () =>{
      try {
      const response = await axios.post(`${base_url}/api/upload`, formData, { 'content-type': 'multipart/form-data'});
        response && setPhoto(response.data)
        setLoad(false)
        setTimeout(()=>{
          window.location.reload()
        }, 1000)
      } catch (error) {
        console.log("error:" + error)
        setLoad(false)
      }
    }
    fetchData();
  }

  useEffect(()=>{
    axios.get(`${base_url}/api/photos`)
    .then((response) =>{
      setPhoto(response.data)
    })
    .catch ((error) =>{
      console.log('oops:',error)
    })
  }, [])

  const Down = (e) =>{
    // console.log(e.currentTarget.name)
    saveAs(e.currentTarget.name, 'image.png')
  }
    
  const Del = (e) =>{
    console.log(e.currentTarget.id)
    axios.delete(`${base_url}/api/delete/${e.currentTarget.id}`)
    .then((res)=>{
      console.log(res.data)
      setTimeout(()=>{
        window.location.reload()
      }, 1000)
    })
    .catch((error) =>{
      console.log(`exception oocured: ${error}`)
    })
  }
  
  return (
    <div>
      {/* <Routes>
        <Route path='/photos' element={<Show />} />
      </Routes> */}
      {
        load ?
        <div style={{ position: 'fixed', backgroundColor: 'rgba(0,0,0,0.8)', top: 0, bottom: 0, left: 0, right: 0, height: '100%', width: '100%', display: 'block' }}>
          <p style={{ color: 'white', marginTop: '45%', textAlign: 'center' }}>loading...</p>
        </div>

        :

        <div>
          <h1 className='text-center justify-center uppercase font-bold text-lg '>image upload</h1>
          <div className='rounded-md shadow-md m-10 overflow-scroll h-[90vh] '>
            <h2 style = {{ textAlign: 'center', margin: 10 }}><b>photos</b><br /><i>click picture to view its original dimension</i></h2>
              
            <div className='flex flex-col  items-center'>
              {
                photo.map((i) =>{
                  // console.log(i)
                  return <div key={i._id}>
                      <a href={i.c_url} >
                        <img className='m-2 justify-center h-[250px] w-[100%]' src={i.c_url} alt="uploads" />                   
                      </a> 
                      <div className='flex flex-row float-right'>
                        <button name={i.c_url} onClick={(e) => Down(e)} className='m-4 flex flex-col'>
                          <FA 
                            name="download"
                            size='2x'
                            style={{ color: 'blue' }}
                          />
                          <i>download</i>
                        </button>
                        <button id={i._id} onClick={(e) => Del(e)} className='m-4 '>
                          <p className='flex flex-col'>
                            <FA 
                              name="remove"
                              size='2x'
                              style={{ color: 'blue' }}
                            />
                            <i>delete</i>
                          </p>
                        </button>
                      </div>
                  </div>
                })
              }
              
            </div>
          </div>
          <div className='m-6 justify-center flex flex-row'>
            <form onSubmit = {(e) => handleUpld(e) } >
              <input type="file" placeholder='Select a picture' encType='multipart/form-data' name='image' onChange={(e) => setFile(e.target.files[0]) } accept='image/*' /> 
              <input type="submit" value="Upload" />
            </form>
          </div>
          <p className='text-center font-ligth text-red-400 m-2'>NOTE: this app also convert the image from any format to PNG</p>
          <p className='text-center font-ligth text-green-500 m-4'>Oops! delete and download button isn't working perfectly? 
            It's due to inconsistency of the CDN platform (cloudinary), 
            cloudinary ougth to provide all those options when image is clicked like FB, 
            the api work perfectly in postman, will use another CDN platform (amazon cloudfront TBP) later.
          </p>
          <div className=''>
            <p className='p-6 text-center'>copyright&nbsp;
            <FA
              name='copyright'
            />&nbsp;
            {
              new Date().getFullYear()
            }&nbsp; 
            ayoadeoye
            </p>
        </div>
        </div>

      }
    </div>
  );
}

export default App;
